import React from "react";
import { DownOutlined } from "@ant-design/icons";
import { Tree } from "antd";
const treeData = [
  {
    title: "Личные вещи",
    key: "0-0",
    children: [
      {
        title: "Одежда, обувь, аксессуары",
        key: "0-0-0",
      },
      {
        title: "Детская одежда и обувь",
        key: "0-0-1",
      },
      {
        title: "Детские товары и Игрушки",
        key: "0-0-2",
      },
      {
        title: "Школьникам",
        key: "0-0-3",
      },
      {
        title: "Часы и украшения",
        key: "0-0-4",
      },
      {
        title: "Товары для красоты и здоровья",
        key: "0-0-5",
      },
      {
        title: "Разные вещи",
        key: "0-0-6",
      },
    ],
  },
];
const Personalitems = () => {
  const onSelect = (selectedKeys, info) => {
    console.log("selected", selectedKeys, info);
  };
  return (
    <Tree
      className="tree"
      switcherIcon={<DownOutlined className="arrow" />}
      defaultExpandedKeys={["0-0-0"]}
      onSelect={onSelect}
      treeData={treeData}
    />
  );
};
export default Personalitems;
