import React from "react";
import { DownOutlined } from "@ant-design/icons";
import { Tree } from "antd";
const treeData = [
  {
    title: "Хобби, Отдых и Спорт",
    key: "0-0",
    children: [
      {
        title: "Антиквариат и Коллекции",
        key: "0-0-0",
      },
      {
        title: "Музыкальные инструменты",
        key: "0-0-1",
      },
      {
        title: "Велосипеды",
        key: "0-0-2",
      },
      {
        title: "Товары для спорта и отдыха",
        key: "0-0-3",
      },
      {
        title: "Книги/Журналы",
        key: "0-0-4",
      },
      {
        title: "CD/DVD/Пластинки/Кассеты",
        key: "0-0-5",
      },
      {
        title: "Билеты и путешествия",
        key: "0-0-6",
      },
      {
        title: "Охота и рыбалка",
        key: "0-0-7",
      },
    ],
  },
];
const Hobbies = () => {
  const onSelect = (selectedKeys, info) => {
    console.log("selected", selectedKeys, info);
  };
  return (
    <Tree
      className="tree"
      switcherIcon={<DownOutlined className="arrow" />}
      defaultExpandedKeys={["0-0-0"]}
      onSelect={onSelect}
      treeData={treeData}
    />
  );
};
export default Hobbies;
