import React from "react";
import { DownOutlined } from "@ant-design/icons";
import { Tree } from "antd";
const treeData = [
  {
    title: "Дом и Сад",
    key: "0-0",
    children: [
      {
        title: "Товары для интерьера",
        key: "0-0-0",
      },
      {
        title: "Товары для сада",
        key: "0-0-1",
      },
      {
        title: "Растения",
        key: "0-0-2",
      },
      {
        title: "Продукты питания",
        key: "0-0-3",
      },
      {
        title: "Посуда и Кухонная утварь",
        key: "0-0-4",
      },
      {
        title: "Хоз.инвентарь и Бытовая химия",
        key: "0-0-5",
      },
      {
        title: "Канцтовары и Расходные ",
        key: "0-0-6",
      },
      {
        title: "Прочие товары для дома ",
        key: "0-0-7",
      },
    ],
  },
];
const HouseandGarden = () => {
  const onSelect = (selectedKeys, info) => {
    console.log("selected", selectedKeys, info);
  };
  return (
    <Tree
      className="tree"
      switcherIcon={<DownOutlined className="arrow" />}
      defaultExpandedKeys={["0-0-0"]}
      onSelect={onSelect}
      treeData={treeData}
    />
  );
};
export default HouseandGarden;
