import React from "react";
import { DownOutlined } from "@ant-design/icons";
import { Tree } from "antd";
const treeData = [
  {
    title: "Электроника",
    key: "0-0",
    children: [
      {
        title: "Телефоны",
        key: "0-0-0",
      },
      {
        title: "Компьютеры",
        key: "0-0-1",
      },
      {
        title: "Ноутбуки, планшеты",
        key: "0-0-2",
      },
      {
        title: "Оргтехника и расходники",
        key: "0-0-3",
      },
      {
        title: "ТВ,Фото,Видео и Аудио",
        key: "0-0-4",
      },
      {
        title: "Игры и Приставки",
        key: "0-0-5",
      },
      {
        title: "Техника для дома и Для себя",
        key: "0-0-6",
      },
      {
        title: "Климатическое оборудование",
        key: "0-0-7",
      },
      {
        title: "Аксессуары и Комплектующие",
        key: "0-0-8",
      },
      {
        title: "Техника для наблюдения",
        key: "0-0-9",
      },
      {
        title: "Прочая электроника",
        key: "0-0-10",
      },
    ],
  },
];
const Electronics = () => {
  const onSelect = (selectedKeys, info) => {
    console.log("selected", selectedKeys, info);
  };
  return (
    <Tree
      className="tree"
      switcherIcon={<DownOutlined className="arrow" />}
      defaultExpandedKeys={["0-0-0"]}
      onSelect={onSelect}
      treeData={treeData}
    />
  );
};
export default Electronics;
