import React from "react";
import { DownOutlined } from "@ant-design/icons";
import { Tree } from "antd";
const treeData = [
  {
    title: "Товары стройки и ремонта",
    key: "0-0",
    children: [
      {
        title: "Двери и окна",
        key: "0-0-0",
      },
      {
        title: "Сантехника",
        key: "0-0-1",
      },
      {
        title: "Стройматериалы",
        key: "0-0-2",
      },
      {
        title: "Инструменты",
      },
    ],
  },
];
const Constructionandrepairgoods = () => {
  const onSelect = (selectedKeys, info) => {
    console.log("selected", selectedKeys, info);
  };
  return (
    <Tree
      className="tree"
      switcherIcon={<DownOutlined className="arrow" />}
      defaultExpandedKeys={["0-0-0"]}
      onSelect={onSelect}
      treeData={treeData}
    />
  );
};
export default Constructionandrepairgoods;
